    body: "As a small business, building a brand should be a goal from day one, when you are starting your business. A brand represents people’s perception of your image, products/services, reputation, values, and motivation so it is important to keep it clearly visible and consistent. Creating a brand that your target market love and want to be associated with makes introducing new products easier and so many other benefits. We have listed some of them here.",
    createdAt: "Jun 14",
    content: [
      {
        heading: "",
        content: "As a small business, building a brand should be a goal from day one, when you are starting your business. A brand represents people’s perception of your image, products/services, reputation, values, and motivation so it is important to keep it clearly visible and consistent. Creating a brand that your target market love and want to be associated with makes introducing new products easier and so many other benefits. We have listed some of them here."
      },     
      {
        heading: "Branding Creates Trust",
        content: "The way you present your company creates an impression and you want it to be one that brings in more clients. Branding helps communicate that you put in effort about what you intend to do."
      },     
      {
        heading: "Branding Improves Recognition",
        content: "Having a clear brand helps customers and the industry identify you quickly, whether it is your image, tone, programs, etc. With consistency, people begin to recognize you and if your communication is clear, it would stay memorable."
      },     
      {
        heading: "Branding Supports Your Marketing Efforts",
        content: "Branding helps your marketing effort and shows you how to market your product and service to reach your target market. You can spend a lot on marketing and if your branding is faulty, it can sabotage your marketing efforts."
      },     
      {
        heading: "Branding Motivates Employees",
        content: "There is a reason why good talent chooses some companies and one of them is brand perception. A company well vested in building a good brand gives the employees a sense of pride to work there. Having a strong brand is said to be essential for employee morale and productivity."
      },     
      {
        heading: "Branding Can Be Monetised",
        content: "If you give consumers a reason to care and feel something about your brand, they have a reason to buy and follow you all the way. Most people make purchasing decisions based on emotional connection to your brand."
      },     
           
    ],
